* {
  margin: 0;
}


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.h_style {
	color: rgb(255, 255, 255);
	text-shadow: 2px 2px  hsl(293, 89%, 11%);
  padding: 25px 0;
  font-family: cursive;
}

body{
  margin:0;
  padding: 0;
  height: 100%;
	background-size: 100% 100% 100% 100%;
	background-image: url("clouds.jpg");
  
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.form__div{
  display: flex ;
  align-items: center;
  flex-direction: row;
  justify-content: space-around;
}

.content {
  flex: 1 0 auto;
  text-align: center;
  background-color: white;
}


footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  background-color:hsl(293, 89%, 11%);
  color: white;
  text-align: center;
  font-size: 12px;
  padding-top: 10px;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

  